@font-face {
  font-family: "ABCFavorit-Regular";
  src: url("../../../Assets/fonts/ABCFavorit-Regular.woff2") format("woff2"),
    url("../../../Assets/fonts/ABCFavorit-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@media screen and (min-width: 1024px) {
  .popup {
    width: 100%;
    position: fixed;
    bottom: 0;
    z-index: 30;

    animation-name: apparition;
    animation-duration: 1000ms;
    animation-timing-function: ease-out;

    .bandeauNoir {
      content: "";
      background-color: black;
      height: 15px;
      width: 100%;
    }

    .contentSection {
      padding: 15px 50px 15px 50px;
      background-color: white;
      display: flex;
      justify-content: space-between;
      height: 11vh;
      border-top: solid;
      align-items: center;

      .left {
        width: 30%;
        height: 64px;

        span {
          font-size: 24px;
          width: 30%;
          line-height: 32px;
          font-family: "ABCFavorit-Regular";
        }
      }

      .middle {
        width: 30%;
        height: 64px;
        position: relative;

        form {
          width: 100%;
          border-bottom: 1px solid black;
          height: 36px;
          position: absolute;
          bottom: 0;

          input {
            width: 93%;
            border: none;
            height: 30px;
          }

          button {
            margin-top: 10px;
            float: right;
          }
        }
      }

      .right {
        position: relative;
        width: 30%;
        height: 64px;

        .closeIcon {
          position: absolute;
          top: 25%;
          right: 0;
        }
      }

      .confirm {
        font-size: 24px;
        line-height: 32px;
        font-family: "ABCFavorit-Regular";
      }
    }
  }
}

// Mobile
@media screen and (max-width: 1023px) {
  .popup {
    background-color: black;
    width: 100%;
    padding: 33px 31px 58px 31px;
    position: fixed;
    bottom: 0;
    box-sizing: border-box;
    z-index: 30;

    animation-name: apparition;
    animation-duration: 1000ms;
    animation-timing-function: ease-out;

    .firstPart {
      position: relative;
      width: 100%;
      height: 32px;
      margin-bottom: 20px;

      img {
        position: absolute;
        top: 0;
        right: 0;
      }
    }

    .secondPart {
      margin-bottom: 20px;

      p {
        color: white;
        font-size: 28px;
        line-height: 36px;
        font-family: "ABCFavorit-Regular";
      }
    }

    .thirdPart {
      width: 100%;

      form {
        border-bottom: 1px solid white;
        width: 100%;
        height: 38px;
        box-sizing: border-box;

        input {
          background-color: black;
          border: none;
          width: 88%;
          height: 28px;
          color: white;
        }

        button {
          margin-top: 10px;
          float: right;
        }
      }
    }

    .confirm {
      color: white;
      font-size: 28px;
      line-height: 36px;
      font-family: "ABCFavorit-Regular";
    }
  }
}

// Animation d'apparition
@keyframes apparition {
  from {
    bottom: -30%;
  }
  to {
    bottom: 0;
  }
}

// Animation d'apparition
@keyframes disparition {
  from {
    bottom: 0;
  }
  to {
    bottom: -30%;
  }
}

.animationDisparition {
  animation-name: disparition;
  animation-duration: 1000ms;
  animation-timing-function: ease-in;
}

.hidden {
  display: none;
}
