.DotsServices{
    position:fixed;
    display: none;
    // background:red;
    right:40px;
    top:50%;
    transform: translateY(-50%);
    // opacity:0;
    z-index:5;
    
    // transition: opacity 1s linear;

    .dot{
        width:11px;
        height:11px;
        border-radius:10000px;
        background:black;
        cursor:pointer;
        color:rgba(0,0,0,0);
    }

    .dot1{
        background:rgba(0, 0, 0, 0.13);
        cursor:pointer;
    }
    .dot2{
        margin-top:46px;
        cursor:pointer;
    }
}


