@import '../../Assets/scss/styles.scss';

.pageCycloramaDesktop{
    .presentation{
        padding-top:252px;
        padding-left:133px;
        padding-right:185px;
        padding-bottom:75px;
        box-sizing: border-box;
        max-width: 100vw;
        display: flex;
        justify-content: space-between;

        .leftColumn{
            width: 140px;

            .titreNosServices{
                font-size: 16px;
                width: 140px;
                float: left;
            }
        }

        .middleColumn{
            width: 575px;

            .mainTitle{
                font-size: 60px;
                min-width: 800px;
                line-height: 1.4;
            }
    
            .subtitle{
                font-size: 60px;
                line-height: 1.4;
            }
    
            .text{
