                line-height: 25px;
            }

            .button{
                background-color: black;
                border-radius: 40px;
                width: 172px;
                text-align: center;
                color: white;
                font-size: 24px;
                margin-top: 15px;
                padding: 10px 0 8px 0;
            }

            .lienPlaquette{
                text-decoration: underline;
                margin-top: 15px;
            }

            .scrollButton{
                margin-left: 380px;
                margin-top: 49px;
                transition: transform 250ms ease;

                &:hover {
                    transform: scale(1.2);
                    cursor: pointer;
                }
            }
        }

        .rightColumn{
            width: 310px;

            img{
                width: 80%;
            }
        }
    }

    .images{
        width: 100vw;
        height: 50vw;
        box-sizing: border-box;
        display: flex;

        .leftBlock{
            height: 50vw;
            width: 50vw;

            img{
                width: 100%;
                height: 100%;
                cursor: pointer;
            }
        }

        .rightBlock{
            height: 50vw;
            width: 50vw;
            display: flex;

            .right1, .right2, .right3, .right4{
                width: 25vw;
                height: 25vw;

                img{
                    width: 100%;
                    height: 100%;
                    cursor: pointer;
                }
            }
        }
    }

    .specifications{
