                    font-size: 1.1vw;
                    line-height: 1.4;
                }
            }

            .separation{
                width: 1px;
                height: 200px;
                background-color: #757575;
            }

            .rightColumn{
                width: 50%;
                box-sizing: border-box;
                padding: 47px 32px 0 32px;

                .text{
                    font-size: 2.5vw;
                    text-align: right;
                }
            }
        }

        hr{
            margin: 32px 0;
            border: 0.5px solid #757575;
        }

        .secondRow{
            display: flex;
            width: 100%;
            box-sizing: border-box;
            
            .leftColumn{
                width: 50%;
                box-sizing: border-box;
                padding: 0 32px;
                max-height: 289px;

                ul{
                    list-style-type: circle;
                    margin-top: 0;

                    li{
                        font-size: 1.9vw;
                        line-height: 1.1;
                    }
                }
            }

            .separation{
                width: 1px;
                height: 289px;
                background-color: #757575;
            }

            .rightColumn{
                width: 50%;
                box-sizing: border-box;
                padding: 0 47px;
                max-height: 289px;

                .flex{
                    width: 100%;
                    box-sizing: border-box;
                    display: flex;

                    .left{
                        width: 50%;
                        box-sizing: border-box;

                        .title{
                            font-size: 40px;
                        }

                        .subtitle{
                            font-size: 15px;
                            margin-top: 4px;
                        }
                    }

                    .right{
                        width: 50%;
                        box-sizing: border-box;
                        text-align: right;

                        .tarifsTitle{
                            font-size: 15px;
                        }

                        .fees{
                            background-color: #252525;
                            padding: 8px 10px 6px 10px;
                            width: fit-content;
                            margin-top: 6px;
                            float: right;
                        }
                    }
                }

                .reserver{
                    background-color: white;
                    width: 170px;
                    height: 170px;
                    border-radius: 50%;
                    margin-left: auto;
                    margin-right: auto;
                    margin-top: 41px;
                    font-size: 28px;
                    color: black;
                    text-align: center;
                    padding: 66px 30px;
                    box-sizing: border-box;
                    transition: transform 250ms ease;

                    &:hover {
                        transform: scale(1.2);
                        cursor: pointer;
                    }
                }
            }
        }

        .thirdRow{
            display: flex;
            width: 100%;
            box-sizing: border-box;

            .leftColumn{
                width: 50%;
                box-sizing: border-box;
                padding: 0 32px;
                font-size: 1.1vw;
                line-height: 1.67;
            }

            .separation{
                width: 1px;
                height: 120px;
                background-color: #757575;
            }

            .rightColumn{
                width: 50%;
                box-sizing: border-box;
                padding: 0 32px;
                text-align: right;
                font-size: 1.1vw;
                line-height: 1.67;
            }
        }

        .rappel{
            font-size: 15px;
            margin: 65px 32px 0 32px;

            span{
                opacity: 0.7;
            }
        }
    }

    .PS_AutresServices{
        background:white;
        padding: 65px 0;
        color:black;

        .PS_AutresServicesWrapper{
            position:relative;
            // max-width:500px;
            margin-left:auto;
            margin-right:auto;

            @include rwd(600){
                margin-left:80px;
            }

            @include lg{
                margin-left:auto;
                margin-right:auto;
                // margin-left:80px;
                margin-left:0px;
            }

            // @include rwd(1250){
            //     margin-left:130px;
            // }
            // @include rwd(1500){
            //     margin-left:180px;
            // }
            // @include rwd(1700){margin-left:300px;}
            // @include rwd(2000){margin-left:320px;}
            // @include rwd(2400){margin-left:410px;}
            

            .PS_TitreSection{
                position:absolute;
                top:-20px;
                left:31px;

                @include lg{
                    top:100px;
                    left:111px;
                }

                @include rwd(1250){
                    left:161px;
                }
                @include rwd(1500){
                    left:211px;
                }
                @include rwd(1700){left:331px;}
                @include rwd(2000){left:351px;}
                @include rwd(2400){left:441px;}
            }
        }

        .sliderNavPC path{
            stroke: black;
        }
    }
}

