@mixin mobile($_max-width) {
    @media screen and (max-width: $_max-width) {
        &{ @content; }
    }
}

.slider{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 100000;
    background-color: rgba(0, 0, 0, 0.7);

    .imagesBoxes{
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        overflow: scroll;
        display: flex;
        -ms-overflow-style: none;
        scrollbar-width: none;
        scroll-snap-type: x mandatory;

        &::-webkit-scrollbar{
            display: none;
        }

        .imageBox{
            min-height: 100%;
            min-width: 100%;
            position: relative;
            overflow: hidden;
            scroll-snap-align: start;

            .imageAndTitle{
                height: 85%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;

                img{
                    height: 95%;
                }
        
                p{
                    margin-top: 10px;
                    font-size: 20px;
                    color: white;
                }
            }
        }
    }

    .navigation{
        position: fixed;
        top: 95vh;
        left: 50%;
        transform: translate(-50%, 0);
        display: flex;

        .points{
            display: flex;
            margin: 10.5px 5px;

            .point{
                content: "";
                height: 9px;
                width: 9px;
                border: 1px solid white;
                border-radius: 50%;
                margin: 0 5px;
                cursor: pointer;
            }
        }

        .leftArrow, .rightArrow{
            cursor: pointer;
        }
        
        .selected{
            background-color: white;
        }
    }

    .close{
        position: absolute;
        top: 25px;
        right: 35px;
        cursor: pointer;
    }

    @include mobile(1023px){
        .imageAndTitle{
            position: absolute !important;
            top: 50% !important;
            height: fit-content !important;
            
            img{
                width: 100vw;
                height: unset !important;
            }
        }
    }
}