@import '../../../Assets/scss/styles.scss';

.imgContainer2{
    overflow: hidden;
    margin-top:31px;
    
    width:calc(100% - 62px);
    margin-left:auto;
    margin-right:auto;
    max-width:380px;
    height:180px;

    @include rwd(340){
        height:185px;
    }
    @include rwd(360){
        height:210px;
    }

    @media screen and (min-height:750px){
        height:250px;
    }

    @include rwd(1150){
        width:unset;
        height:unset;
        max-width: unset;
        margin:unset;

        height:500px;
        // min-height:400px;
        width:360px;
        
    }

    @include qhd{
        height:700px;
        width:520px;
    }



        img{
            object-fit: cover;
            width: 100%;
            height: 100%;  
            margin-top:70px;
            @include rwd(1150){
                margin-top:unset;
            }         
        }
}